<template>
<teleport to="body">
    <div v-if="show" @click="tryClose" class="backdrop"></div>
    <transition name="dialog">
        <dialog open v-if="show">
            <header>
                <slot name="header">
                    <h2>{{ title }}</h2>
                </slot>
            </header>
            <section>
                <slot></slot>
            </section>
            <menu v-if="!fixed">
                <slot name="actions">
                    <base-button @click="tryClose">Close</base-button>
                </slot>
            </menu>
        </dialog>
    </transition>
</teleport>
</template>

<script>
export default {
    props: {
        show: {
            type: Boolean,
            required: true,
        },
        title: {
            type: String,
            required: false,
        },
        fixed: {
            type: Boolean,
            required: false,
            default: false,
        },
    },
    emits: ['close'],
    methods: {
        tryClose() {
            if (this.fixed) {
                return;
            }
            this.$emit('close');
        },
    },
};
</script>

<style scoped>
@import '../../styles/base-dialog.css';
</style>
